$OSRegular: "OpenSansRegular";
$OSSemibold: "OpenSansSemibold";
$textc-grey: #4e525d;
$turquoise: #00CCCE;
$grey-bg: #F1F1F1;

.clr_ln:after {
  content: '';
  clear: both;
  display: table;
  width: 100%;
}

.wrapper_page{
  overflow: hidden;
}
a {
  outline-style: none;
  &:focus {
    outline-style: none;
  }
}
#arrow_up{
  position: fixed;
  cursor: pointer;
  right: 10px;
  bottom: 30px;
  width: 50px;
  height: 50px;
  background: $turquoise;
  color: #fff;
  text-align: center;
  border-right: 0 solid;
  border-radius: 25px;
  padding: 5px 0 0 0;
  z-index: 30;
  &:hover{
    background: #009C9E;
  }
}
.title {
  font: 42px/48px $OSRegular, Arial, Helvetica, sans-serif;
}

#header {
  .container {
    padding-top: 31px;
    padding-bottom: 31px;
  }
}

#logo {
  float: left;
}
.hamburger{
  display: none;
  float: right;
  .line{
    width: 40px;
    height: 5px;
    background-color: $turquoise;
    display: block;
    margin: 8px auto;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  &:hover{
    cursor: pointer;
  }
}
#hamburger.is-active{
  .line:nth-child(1){
    -webkit-transform: translateY(13px) rotate(45deg);
    -ms-transform: translateY(13px) rotate(45deg);
    -o-transform: translateY(13px) rotate(45deg);
    transform: translateY(13px) rotate(45deg);
  }
  .line:nth-child(2){
    opacity: 0;
  }
  .line:nth-child(3){
    -webkit-transform: translateY(-13px) rotate(-45deg);
    -ms-transform: translateY(-13px) rotate(-45deg);
    -o-transform: translateY(-13px) rotate(-45deg);
    transform: translateY(-13px) rotate(-45deg);
  }
}
#nav {
  float: right;
  margin: 10px 0 0 0;
  ul {
    padding: 0;
    margin: 0;
    list-style: none;
    font-size: 0;
    letter-spacing: 0;
    line-height: 0;
    li {
      display: inline-block;
      vertical-align: top;
      a {
        text-decoration: none;
        color: $textc-grey;
        padding: 40px 20px 35px 20px;
        font: 15px/19px $OSRegular, Arial, Helvetica, sans-serif;
        &:hover {
          color: $turquoise;
          border-bottom: 3px solid $turquoise;
          background: #f8f9fb;
        }
      }
    }
  }
}

.carousel_bg{
  background-image: url(../img/slide-img.jpg);
  position: relative;
  .container{
    position: static;
  }
}
.carousel_bg,
.imgs_settings,
.rates_settings{
  margin: 0 auto;
  max-width: 1440px;
}
.carousel_settings{
  position: static;
}
#owl-carousel {
  position: static !important;
  .owl-item {
    padding: 130px 0 146px 0;
    position: static !important;
  }
  & div {
    text-align: center;
  }
  .sld_cont {
    text-align: center;
    display: inline-block;
    width: 675px;
    h1 {
      font: 63px/69px $OSSemibold, Arial, Helvetica, sans-serif;
      margin: 0 0 38px 0;
      color: #fff;
    }
    h2 {
      font: 23px/29px $OSRegular, Arial, Helvetica, sans-serif;
      color: #fff;
      margin: 0 0 60px 0;
      padding: 0 140px;
    }
    a {
      font: 26px/30px $OSSemibold, Arial, Helvetica, sans-serif;
      color: #fff;
      text-decoration: none;
      border: 3px solid #fff;
      border-radius: 4px;
      padding: 15px 40px;
      display: inline-block;
      &:hover {
        background: #fdfdfd;
        color: $turquoise;
      }
    }
  }
  .owl-nav{
    position: relative;
  }
  .owl-prev,
  .owl-next {
    position: absolute;
    top: -300px;
    color: #00CCCE;
    background: #fff;
    width: 50px;
    border: 0 solid #fff;
    border-radius: 60px;
    text-align: center;
    vertical-align: middle;
    height: 50px;
    &:hover {
      background: $turquoise;
      i {
        color: #fff;
      }
    }
  }
  .owl-prev {
    left: -20px;
  }
  .owl-next {
    right: -20px;
  }
}

.box_border {
  width: 104px;
  height: 104px;
  border: 2px solid $turquoise;
  border-radius: 60px;
  text-align: center;
  color: $turquoise;
  margin: 0 auto 40px auto;
  i {
    margin: 32px 0 0 0;
  }
}

.block_boxes {
  padding: 102px 0 97px;
  .box_cont {
    text-align: center;
    font: 14px/18px $OSRegular, Arial, Helvetica, sans-serif;
    color: $textc-grey;
    h1 {
      font: 22px/28px $OSRegular, Arial, Helvetica, sans-serif;
      color: $textc-grey;
      margin: 0 0 23px 0;
    }
    p {
      margin: 0 0 30px 0;
    }
    a {
      font: 14px/18px $OSRegular, Arial, Helvetica, sans-serif;
      color: #fff;
      background: $turquoise;
      padding: 8px 15px;
      text-decoration: none;
      border: 0 solid #fff;
      border-radius: 5px;
      display: inline-block;
    }
  }
}
.imgs_settings,
.rates_settings{
  background: $grey-bg;
}
.imgs_block {
  padding: 45px 15px 102px;
  h1 {
    text-align: center;
    margin: 0 0 60px 0;
    font: 42px/48px $OSRegular, Arial, Helvetica, sans-serif;
    color: $textc-grey;
  }
}

.first_line_img {
  margin: 0 0 28px 0;
  img {
    margin-bottom: 25px;
  }
}

.imgs_link_box {
  text-align: center;
}

.imgs_link {
  font: 24px/28px $OSSemibold, Arial, Helvetica, sans-serif;
  color: #fff;
  background: $turquoise;
  border: 0 solid $turquoise;
  border-radius: 4px;
  padding: 21px 41px;
  text-decoration: none;
  &:hover {
    color: #fff;
  }
}

.members_block {
  padding: 90px 15px 102px;
  h1 {
    font: 42px/46px $OSRegular, Arial, Helvetica, sans-serif;
    color: $textc-grey;
    margin: 0 0 135px 0;
    text-align: center;
  }
}

.mb_cont_box {
  font: 14px/18px $OSRegular, Arial, Helvetica, sans-serif;
  color: $textc-grey;
  text-align: center;
  h2 {
    font: 22px/26px $OSRegular, Arial, Helvetica, sans-serif;
    color: $textc-grey;
    margin: 0 0 10px 0;
  }
  h3 {
    margin: 0 0 30px 0;
    font: 14px/18px $OSRegular, Arial, Helvetica, sans-serif;
  }
  p {
    margin: 0;
  }
}

.mb_box {
  width: 201px;
  height: 201px;
  border: 0 solid #fff;
  border-radius: 100px;
  background: #EFEFF1;
  margin: 0 auto 65px;
  cursor: pointer;
  &:hover {
    i {
      display: inline-block;
      margin-top: 75px;
      color: #ff6d6d;
    }
  }
  i {
    display: none;
  }
}
.rates_block {
  background: $grey-bg;
  padding: 118px 0 100px;
}

.rate_box {
  text-align: center;
  font: 14px/18px $OSRegular, Arial, Helvetica, sans-serif;
  color: #8c9299;
  //background: #F8F9FB;
  header {
    background: #405360;
    padding: 21px 0 24px 0;
    h1 {
      font: 26px/30px $OSRegular, Arial, Helvetica, sans-serif;
      color: #fff;
      margin: 0;
    }
  }
  h2 {
    font: 20px/26px $OSRegular, Arial, Helvetica, sans-serif;
    color: $textc-grey;
    background: #F8F9FB;
    padding: 25px 0 24px 0;
    margin: 0;
  }
  ul {
    padding: 0;
    margin: 0;
    list-style: none;
    background: #fff;
    li {
      border: #EBF0F1 solid 1px;
      padding: 13px 0 14px 0;
    }
  }
  section {
    background: #F8F9FB;
    padding: 29px 0;
    a {
      padding: 7px 49px 13px;
      background: #738594;
      font: 17px/21px $OSSemibold, Arial, Helvetica, sans-serif;
      color: #fff;
      text-decoration: none;
      display: inline-block;
      border: 0 solid;
      border-radius: 4px;
    }
  }
  &:hover {
    header,
    a {
      background: $turquoise;
    }
  }
}

.clients_block {
  padding: 135px 15px 97px;
  position: relative;
}
.clients_settings{
  position: static;
}
#carousel-clients {
  position: static !important;
  .owl-item{
    position: static !important;
  }
  & div {
    font: 19px/25px $OSRegular, Arial, Helvetica, sans-serif;
    color: $textc-grey;
    text-align: center;
    //padding:0 40px;
    h1 {
      font: 42px/46px $OSRegular, Arial, Helvetica, sans-serif;
      color: $textc-grey;
      margin: 0 0 32px 0;
    }
    p {
      margin: 0 0 33px 0;
    }
    span {
      font: 21px/25px $OSRegular, Arial, Helvetica, sans-serif;
      color: #9a8faf;
    }
  }
  .owl-nav{
    position: relative;
  }
  .owl-prev,
  .owl-next {
    position: absolute;
    top: -40px;
    color: #989898;
  }
  .owl-prev {
    left: 10px;
  }
  .owl-next {
    right: 10px;
  }
}
#footer {
  background: url(../img/footer_bg.jpg) no-repeat;
  background-size: cover;
  padding: 98px 0;
  h1 {
    font: 42px/46px $OSRegular, Arial, Helvetica, sans-serif;
    color: #fff;
    text-align: center;
    margin: 0 0 111px 0;
  }
  .contact_cont {
    font: 14px/18px $OSRegular, Arial, Helvetica, sans-serif;
    padding: 0 100px 0 0;
    h2,
    p {
      color: #fff;
    }
    h2 {
      font: 24px/28px $OSRegular, Arial, Helvetica, sans-serif;
      margin: 0 0 14px 0;
    }
    p {
      margin: 0 0 42px 0;
    }
  }
  .social_lks {

  }
  .lk_facebook,
  .lk_twitter,
  .lk_google {
    width: 42px;
    height: 42px;
    display: inline-block;
    text-align: center;
    border: 0 solid;
    border-radius: 21px;
    padding: 10px 0 0 0;
    margin: 0 14px 20px 0;
  }
  .lk_facebook {
    background: #23599A;
    &:hover {
      background: #043A7B;
    }
    i {
      color: #fff;
    }
  }
  .lk_twitter {
    background: #00C3F5;
    &:hover {
      background: #2093C5;
    }
    i {
      color: #fff;
    }
  }
  .lk_google {
    background: #FF4B3E;
    &:hover {
      background: #CF1B0E;
    }
    i {
      color: #fff;
    }
  }
  .contact_box {
    padding: 0 200px 0 0;
    width: 100%;
    a,
    span {
      display:inline-block;
      color: #bface2;
      margin: 0 30% 10px 0;
    }
  }
  .form_box {
    padding:0;
  }
}
.submission-form {
  textarea {
    padding: 20px 5px 5px 17px;
    width: 571px !important;
    height: 178px;
    margin: 0 0 30px 0;
    border: 0 solid;
    border-radius: 3px !important;
    &::placeholder{
      font: 16px/20px $OSRegular, Arial, Helvetica, sans-serif;
      color: #c1c1c1;
    }
  }
  input[type="submit"]{
    display: inline-block;
    width: 170px;
    float: right;
    margin: 0 12px 0 0;
    font: 22px/26px $OSRegular, Arial, Helvetica, sans-serif;
    color: #fff;
    padding: 11px 64px 16px 52px;
    background: $turquoise;
    border: 0 solid;
    border-radius: 3px;
  }
}
.form-info {
  padding: 12px 10px 12px 16px;
  margin: 0 0 30px 0;
  width: 269px !important;
  height: 44px;
  border: 0 solid;
  border-radius: 3px !important;
  &::placeholder{
    font: 16px/20px $OSRegular, Arial, Helvetica, sans-serif;
    color: #c1c1c1;
  }
  &:first-child{
    margin: 0 30px 30px 0;
  }
}
