/*==========  Desktop First Method  ==========*/

@media only screen and (max-width : 1440px) {

}
/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {
  .block_boxes{
    padding: 102px 70px 47px;
    .box_cont{
      margin-bottom: 50px;
    }
  }
  .members_block{
    padding: 90px 0 52px;
  }
  .mb_cont_box{
    margin-bottom: 50px;
  }
  .rates_block{
    padding: 118px 0 50px;
  }
  .rate_box{
    margin-bottom: 50px;
  }
  .contact_cont{
    padding: 0 50px 0 0;
  }
  #footer{
    .contact_box{
      padding: 0 100px 0 0;
    }
  }
  .form-info{
    margin: 0 0 30px 0;
    &:first-child{
      margin: 0 24px 30px 0;
    }
  }
}


/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {
  #owl-carousel{
    .sld_cont{
      h1{
        font: 58px/64px "OpenSansSemibold",Arial,Helvetica,sans-serif;
      }
      h2{
        font: 22px/27px "OpenSansRegular",Arial,Helvetica,sans-serif;
      }
      a{
        font: 24px/28px "OpenSansSemibold",Arial,Helvetica,sans-serif;
        padding: 12px 35px;
      }
    }
  }
  .imgs_link{
    font: 22px/26px "OpenSansSemibold",Arial,Helvetica,sans-serif;
    padding: 19px 36px;
  }
  .submission-form{
    textarea{
      width: 371px !important;
    }
  }
  .form-info{
    width: 369px !important;
  }
}

/* Small Devices, Tablets */
@media only screen and (max-width : 768px) {
  .hamburger{
    display: block;
  }
  #nav{
    position: absolute;
    z-index:20;
    top: 77px;
    right: 10px;
    background: #fff;
    margin: 0;
    ul{
      display: none;
      li{
        display: block;
        margin: 0 0 10px 0;
        text-align: center;
        padding: 10px 0;
        &:hover{
          background: #f8f9fb;
          a{
            color: $turquoise;
          }
        }
        a{
          padding: 0 50px;
          &:hover{
            border-bottom: none;
            background: none;
          }
        }
      }
    }
  }
  #owl-carousel{
    .sld_cont{
      width: 100%;
      h1{
        font: 36px/45px "OpenSansSemibold",Arial,Helvetica,sans-serif;
        margin: 0 0 35px 0;
      }
      h2{
        font: 20px/25px "OpenSansRegular",Arial,Helvetica,sans-serif;
        padding: 0 90px;
        margin: 0 0 70px 0;
      }
      a{
        font: 20px/25px "OpenSansSemibold",Arial,Helvetica,sans-serif;
      }
    }
    .owl-prev{
      left: -10px;
    }
    .owl-next{
      right: -10px;
    }
  }
  .first_line_img{
    img{
      margin:0 auto 30px;
    }
  }
  #carousel-clients{
    .owl-prev{
      left: 30px;
    }
    .owl-next{
      right: 30px;
    }
  }
  #footer{
    padding: 90px 0;
    h1{
      margin: 0 0 90px 0;
    }
    .contact_cont{
      padding: 0;
    }
    .lk_facebook,
    .lk_twitter,
    .lk_google {
      margin: 0 10px 20px 0;
    }
    .social_lks{
      & a:last-child{
        margin: 0 0 20px 0;
      }
    }
    .contact_box{
      margin-bottom: 40px;
      text-align: left;
      padding: 0;
      a,
      span {
        display:inline-block;
        color: #bface2;
        margin: 0 5% 10px 0;
      }
    }
  }

  .form_box{
    form{
      //margin: 0 auto;
    }
  }
  .submission-form{
    //text-align: center;
    textarea{
      width: 311px !important;
      display: inline-block;
    }
    input[type="submit"]{
      float: left;
    }
  }
  .form-info{
    width: 310px !important;
  }
}

/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px) {
  #arrow_up{
    bottom: 10px;
    width: 40px;
    height: 40px;
    padding: 0;
  }
  #owl-carousel{
    .owl-item{
      padding: 40px 0 40px 0;
    }
    .sld_cont{
      h1{
        font: 30px/40px "OpenSansSemibold",Arial,Helvetica,sans-serif;
        margin: 0 0 30px 0;
      }
      h2{
        font: 18px/23px "OpenSansRegular",Arial,Helvetica,sans-serif;
        padding: 0 30px;
        margin: 0 0 50px 0;
      }
    }
    .owl-prev,
    .owl-next{
      font-size: 10px;
      width: 40px;
      height: 40px;
      top: -140px;
    }
    //.owl-prev{
    //  left: px;
    //}
    //.owl-next{
    //  right: 5px;
    //}
  }
  .block_boxes{
    padding: 60px 70px 47px;
  }
  .clients_block{
    padding: 90px 15px 90px;
  }
  #carousel-clients{
    div{
      font: 16px/24px "OpenSansRegular",Arial,Helvetica,sans-serif;
      h1{
        font: 36px/46px "OpenSansRegular",Arial,Helvetica,sans-serif;
      }
    }
    .owl-prev,
    .owl-next{
      padding: 10px;
    }
  }
  .submission-form{
    textarea{
      width: 311px !important;
    }
  }
}
